import { Injectable } from '@angular/core';
import { AccountStatementDataService, getTotalCountFromHeaders } from '@backbase/account-statement-common-ang';

import { of } from 'rxjs';
import { catchError, map, switchMap, withLatestFrom } from 'rxjs/operators';
import { loadAccountStatementsBusinessToRequest } from '../helpers/mappers';
import {
  loadAccountStatementsBusiness,
  loadAccountStatementsBusinessSuccess,
  loadAccountStatementsFailure,
} from './actions';
import {
  accountStatementsAccountIds,
  accountStatementsFilterParams,
  accountStatementsPaginationParams,
  accountStatementsSortingParams,
  accountStatementsWidgetPreferences,
} from './selectors';
import { Actions, createEffect, ofType } from '@ngrx/effects';
import { Store } from '@ngrx/store';
import { AccountStatementStoreState } from '../account-statement.reducer';

@Injectable()
export class AccountStatementEffects {
  accountStatementsBusiness$ = createEffect(() =>
    this.actions$.pipe(
      ofType(loadAccountStatementsBusiness),
      withLatestFrom(
        this.store.select(accountStatementsAccountIds),
        this.store.select(accountStatementsFilterParams),
        this.store.select(accountStatementsSortingParams),
        this.store.select(accountStatementsPaginationParams),
        this.store.select(accountStatementsWidgetPreferences),
      ),
      switchMap(([action, accountIds, filterParams, sortingParams, paginationParams, widgetPreferences]) => {
        const { append, ...actionRequestParams } = action;
        return this.dataService
          .getAccountStatementsBusiness(
            loadAccountStatementsBusinessToRequest(
              {
                accountIds,
                filterParams,
                sortingParams,
                paginationParams,
                ...actionRequestParams,
              },
              widgetPreferences,
            ),
          )
          .pipe(
            map(response =>
              loadAccountStatementsBusinessSuccess({
                accountStatements: response.body,
                totalCount: getTotalCountFromHeaders(response.headers),
                ...action,
              }),
            ),
            catchError(error => of(loadAccountStatementsFailure({ error }))),
          );
      }),
    ),
  );

  constructor(
    private readonly actions$: Actions,
    private readonly dataService: AccountStatementDataService,
    private readonly store: Store<AccountStatementStoreState>,
  ) {}
}
