import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { AccountStatement } from '@backbase/data-ang/account-statements';
import {
  accountStatements,
  accountStatementsError,
  accountStatementsFilterParams,
  accountStatementsLoading,
  accountStatementsPaginationParams,
  accountStatementsSortingParams,
  accountStatementsTotalCount,
  accountStatementsWidgetPreferences,
} from './account-statements/selectors';
import { Categories, LoadingState } from '@backbase/account-statement-common-ang';
import { PaginationParams } from './models/pagination-params.model';
import { AccountStatementsFilterParams, SortingParams, WidgetPreferences } from './account-statements/reducers';
import { AccountStatementStoreState } from './account-statement.reducer';
import { selectCategories } from './categories/selectors';
import { loadAccountStatementsBusiness, updateWidgetPreferences } from './account-statements/actions';
import {
  LoadAccountStatementsBusinessPayload,
  UpdateWidgetPreferencesPayload,
} from './models/account-statements-actions.model';
import { loadCategories } from './categories/actions';
import { Store } from '@ngrx/store';

@Injectable()
export class AccountStatementStoreModel {
  readonly accountStatements$: Observable<AccountStatement[]> = this.store.select(accountStatements);
  readonly accountStatementsLoading$: Observable<LoadingState> = this.store.select(accountStatementsLoading);
  readonly accountStatementsError$ = this.store.select(accountStatementsError);
  readonly accountStatementsTotalCount$ = this.store.select(accountStatementsTotalCount);
  readonly accountStatementsPaginationParams$: Observable<PaginationParams | undefined> = this.store.select(
    accountStatementsPaginationParams,
  );
  readonly accountStatementsSortingParams$: Observable<SortingParams | undefined> =
    this.store.select(accountStatementsSortingParams);
  readonly accountStatementsFilterParams$: Observable<AccountStatementsFilterParams> =
    this.store.select(accountStatementsFilterParams);
  readonly accountStatementsWidgetPreferences$: Observable<WidgetPreferences> = this.store.select(
    accountStatementsWidgetPreferences,
  );
  readonly categories$: Observable<Categories> = this.store.select(selectCategories);

  constructor(private readonly store: Store<AccountStatementStoreState>) {}

  dispatchLoadAccountStatementsBusiness(payload: LoadAccountStatementsBusinessPayload = {}) {
    this.store.dispatch(loadAccountStatementsBusiness(payload));
  }

  dispatchUpdateWidgetPreferences(payload: UpdateWidgetPreferencesPayload) {
    this.store.dispatch(updateWidgetPreferences(payload));
  }
  dispatchLoadCategories() {
    this.store.dispatch(loadCategories());
  }
}
