import { Injectable } from '@angular/core';
import { AccountStatementDataService } from '@backbase/account-statement-common-ang';

import { of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';
import { loadCategories, loadCategoriesFailure, loadCategoriesSuccess } from './actions';
import { Actions, createEffect, ofType } from '@ngrx/effects';

@Injectable()
export class CategoriesEffects {
  loadCategories$ = createEffect(() =>
    this.actions$.pipe(
      ofType(loadCategories),
      switchMap(() =>
        this.dataService.loadCategories().pipe(
          map(response => loadCategoriesSuccess({ categories: response })),
          catchError(error => of(loadCategoriesFailure({ error }))),
        ),
      ),
    ),
  );

  constructor(private readonly actions$: Actions, private readonly dataService: AccountStatementDataService) {}
}
