import { createAction, props } from '@ngrx/store';
import { Categories } from '@backbase/account-statement-common-ang';

export const loadCategories = createAction('[AS Account Statements] Load Categories for Business');
export const loadCategoriesSuccess = createAction(
  '[AS Account Statements] Load Categories for Business Success',
  props<{ categories: Categories }>(),
);
export const loadCategoriesFailure = createAction(
  '[AS Account Statements] Load Categories for Business Failure',
  props<{ error: Error }>(),
);
