import { createAction, props } from '@ngrx/store';
import {
  LoadAccountStatementsBusinessPayload,
  LoadAccountStatementsBusinessSuccessPayload,
  UpdateWidgetPreferencesPayload,
} from '../models/account-statements-actions.model';

export const loadAccountStatementsBusiness = createAction(
  '[AS Account Statements] Load Account Statements for Business',
  props<LoadAccountStatementsBusinessPayload>(),
);

export const loadAccountStatementsBusinessSuccess = createAction(
  '[AS Account Statements] Load Account Statements for Business Success',
  props<LoadAccountStatementsBusinessSuccessPayload>(),
);

export const loadAccountStatementsFailure = createAction(
  '[AS Account Statements] Load Account Statements Failure',
  props<{ error: Error }>(),
);
export const updateWidgetPreferences = createAction(
  '[AS Accounts] Update Widget Preferences',
  props<UpdateWidgetPreferencesPayload>(),
);
