import { Categories } from '@backbase/account-statement-common-ang';
import { createSelector } from '@ngrx/store';
import { selectAccountStatements } from '../account-statement.selector';

export const selectCategoriesState = createSelector(
  selectAccountStatements,
  accountStatementsState => accountStatementsState.categories,
);

export const selectCategories = createSelector(selectCategoriesState, (state): Categories => state.data);

export const selectCategoriesError = createSelector(selectCategoriesState, state => state.error);
