import { LoadingState } from '@backbase/account-statement-common-ang';
import { AccountStatement } from '@backbase/data-ang/account-statements';
import { createSelector } from '@ngrx/store';
import { AccountStatementsState } from './reducers';
import { selectAccountStatements } from '../account-statement.selector';

export const selectAccountStatementsState = createSelector(
  selectAccountStatements,
  accountStatementsState => accountStatementsState.accountStatements,
);

export const accountStatements = createSelector(
  selectAccountStatementsState,
  (state): AccountStatement[] => state.data,
);

export const accountStatementsLoading = createSelector(
  selectAccountStatementsState,
  (state: AccountStatementsState): LoadingState => state.loading,
);

export const accountStatementsError = createSelector(selectAccountStatementsState, state => state.error);

export const accountStatementsTotalCount = createSelector(selectAccountStatementsState, state => state.totalCount);

export const accountStatementsAccountIds = createSelector(selectAccountStatementsState, state => state.accountIds);

export const accountStatementsPaginationParams = createSelector(
  selectAccountStatementsState,
  state => state.paginationParams,
);

export const accountStatementsSortingParams = createSelector(
  selectAccountStatementsState,
  state => state.sortingParams,
);

export const accountStatementsFilterParams = createSelector(selectAccountStatementsState, state => state.filterParams);
export const accountStatementsWidgetPreferences = createSelector(
  selectAccountStatementsState,
  state => state.widgetPreferences,
);
