import { Action, createReducer, on } from '@ngrx/store';
import { loadCategoriesFailure, loadCategoriesSuccess } from './actions';
import { Categories } from '@backbase/account-statement-common-ang';

/**
 * State model for categories of account statements.
 */
export interface CategoriesState {
  /**
   * List of available categories for statements.
   */
  data: Categories;
  /**
   * Error that might occur during loading of the categories.
   */
  error?: Error;
}

export const categoriesInitialState: CategoriesState = {
  data: [],
  error: undefined,
};

const reducer = createReducer(
  categoriesInitialState,
  on(loadCategoriesSuccess, (state, { categories }) => ({
    ...state,
    data: categories,
  })),
  on(loadCategoriesFailure, (state, { error }) => ({
    ...state,
    data: [],
    error,
  })),
);

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
export function categoriesReducer(state: CategoriesState | undefined, action: Action) {
  return reducer(state, action);
}
