import { NgModule } from '@angular/core';
import { AccountStatementCommonModule } from '@backbase/account-statement-common-ang';
import { StoreModule } from '@ngrx/store';

import { AccountStatementEffects } from './account-statements/effects';
import { CategoriesEffects } from './categories/effects';
import { ACCOUNT_STATEMENTS_FEATURE_KEY, accountStatementsReducers } from './account-statement.reducer';
import { EffectsModule } from '@ngrx/effects';

@NgModule({
  imports: [
    StoreModule.forFeature(ACCOUNT_STATEMENTS_FEATURE_KEY, accountStatementsReducers),
    EffectsModule.forFeature([AccountStatementEffects, CategoriesEffects]),
    AccountStatementCommonModule,
  ],
})
export class AccountStatementStoreModule {}
