import {
  accountStatementsInitialState,
  accountStatementsReducer,
  AccountStatementsState,
} from './account-statements/reducers';
import { categoriesInitialState, categoriesReducer, CategoriesState } from './categories/reducers';

/**
 * Global state for the Account Statements business journey.
 *
 * @public
 */
export interface AccountStatementStoreState {
  /** {@inheritDoc AccountStatementsState} */
  accountStatements: AccountStatementsState;

  /** {@inheritDoc CategoriesState} */
  categories: CategoriesState;
}

export const initialState: AccountStatementStoreState = {
  accountStatements: accountStatementsInitialState,
  categories: categoriesInitialState,
};

/** Key for feature state used in NgRx StoreModule. */
export const ACCOUNT_STATEMENTS_FEATURE_KEY = 'accountStatements';

export const accountStatementsReducers = {
  accountStatements: accountStatementsReducer,
  categories: categoriesReducer,
};
