import { Categories, DatePeriods, LoadingState, today, unixZeroDay } from '@backbase/account-statement-common-ang';
import { Action, createReducer, on } from '@ngrx/store';
import { LoadAccountStatementsBusinessPayload } from '../models/account-statements-actions.model';
import {
  loadAccountStatementsBusiness,
  loadAccountStatementsBusinessSuccess,
  loadAccountStatementsFailure,
  updateWidgetPreferences,
} from './actions';
import { SortEvent } from '@backbase/ui-ang/table';
import { AccountStatement } from '@backbase/data-ang/account-statements';
import { PaginationParams } from '../models/pagination-params.model';

/**
 * State model for sorting parameters;
 *
 * @public
 */
export type SortingParams = SortEvent;

/**
 * State model for parameters used for filtering account statements.
 *
 * @public
 */
export interface AccountStatementsFilterParams {
  /**
   * Start position of the date filter.
   */
  dateFrom?: string;
  /**
   * End position of the date filter.
   */
  dateTo?: string;
  /**
   * Predefined date range.
   */
  datePeriod?: DatePeriods;
  /**
   * Selected categories in the filter.
   */
  category?: Categories;
}

/**
 * State model for preferences of a widget, minimum and maximum date to be set in filter.
 *
 * @public
 */
export interface WidgetPreferences {
  /**
   * Minimal date that can be selected in date filter.
   */
  minimalDate: Date;
  /**
   * Maximum date that can be selected in date filter.
   */
  maximumDate: Date;
}

/**
 * State model for account statements.
 *
 * @public
 */
export interface AccountStatementsState {
  /** {@inheritDoc AccountStatement} */
  data: AccountStatement[];
  /** {@inheritDoc LoadingState} */
  loading: LoadingState;
  /** {@inheritDoc Error} */
  error?: Error;
  /**
   * Parameter that contains total count of statements in the current search.
   */
  totalCount: number;
  /**
   * Parameter that keeps list of selected account IDs.
   */
  accountIds: string[];
  /** {@inheritDoc AccountStatementsFilterParams} */
  filterParams: AccountStatementsFilterParams;
  /** {@inheritDoc SortingParams} */
  sortingParams?: SortingParams;
  /** {@inheritDoc PaginationParams} */
  paginationParams?: PaginationParams;
  /** {@inheritDoc WidgetPreferences} */
  widgetPreferences: WidgetPreferences;
}

export const accountStatementsInitialState: AccountStatementsState = {
  data: [],
  totalCount: 0,
  loading: LoadingState.IDLE,
  error: undefined,
  accountIds: [],
  filterParams: {
    datePeriod: DatePeriods.AllTime,
  },
  widgetPreferences: {
    minimalDate: unixZeroDay(),
    maximumDate: today(),
  },
};

const smartLoadingState = (
  state: AccountStatementsState,
  action: LoadAccountStatementsBusinessPayload,
): LoadingState => {
  if (action.accountIds && action.accountIds !== state.accountIds) return LoadingState.LOADING;
  if (action.filterParams && action.filterParams !== state.filterParams) return LoadingState.LOADING_FILTERED;
  if (action.append) return LoadingState.LOADING_MORE;
  if (
    (action.sortingParams && action.sortingParams !== state.sortingParams) ||
    (action.paginationParams && action.paginationParams !== state.paginationParams)
  ) {
    return LoadingState.LOADING_TABLE;
  }
  return LoadingState.LOADING;
};

const reducer = createReducer(
  accountStatementsInitialState,
  on(loadAccountStatementsBusiness, (state, action) => ({
    ...state,
    loading: smartLoadingState(state, action),
    error: undefined,
  })),
  on(loadAccountStatementsBusinessSuccess, (state, action) => {
    const { accountStatements, accountIds, filterParams, paginationParams, sortingParams, totalCount, append } = action;
    const dataAppendTo = append ? state.data : [];

    return {
      ...state,
      loading: LoadingState.IDLE,
      data: [...dataAppendTo, ...accountStatements],
      totalCount,
      ...(accountIds && { accountIds }),
      ...(filterParams && { filterParams }),
      ...(sortingParams && { sortingParams }),
      ...(paginationParams && { paginationParams }),
    };
  }),
  on(loadAccountStatementsFailure, (state, action) => ({
    ...state,
    error: action.error,
    loading: LoadingState.IDLE,
  })),
  on(updateWidgetPreferences, (state, action) => ({
    ...state,
    widgetPreferences: { ...state.widgetPreferences, ...action.widgetPreferences },
  })),
);

// eslint-disable-next-line prefer-arrow/prefer-arrow-functions
export function accountStatementsReducer(state: AccountStatementsState | undefined, action: Action) {
  return reducer(state, action);
}
